<template>
    <div class="container">
      <div class="row">
        <EnemyMenu/>
      </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import EnemyMenu from '@/components/EnemyMenu.vue'
import { useLanguage } from '@/store/Language'
import { useInitUnit } from "@/store/InitUnit"
import { storeToRefs } from 'pinia'
import Enemies from '@/assets/json/Enemies.json'

export default {
  name: 'EnemiesView',
  components: {
      EnemyMenu,
  },
  setup() {
    console.log(useRoute().params.heroName)
    const { language } = storeToRefs(useLanguage())

    watch(language, () => {
        useLanguage().changeLanguage()
    })

    const { unit } = storeToRefs(useInitUnit())
    unit.value = useRoute().params.heroName === undefined
      ? Enemies[11] // Alice
      : Enemies[Enemies.findIndex(obj => 
          (obj.Cname.toLowerCase() === useRoute().params.heroName.toString().toLowerCase()) || 
          (obj.Cname.replace(" ", "").toLowerCase() === useRoute().params.heroName.toString().toLowerCase())
        )]
    if (unit.value === undefined) {
      unit.value = Enemies[11]
      useLanguage().changeLanguage()
      useRouter().push({ path:'/' })
    } else {
      console.log(unit.value)
      useLanguage().changeLanguage()
    }
  }
}
</script>
